import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})

export class DataService {

  public dataSend: BehaviorSubject<any> = new BehaviorSubject(null);

  constructor() { }
  
  getData(){
    return this.dataSend.asObservable();
  }




  
  

}
